<template>
  <div class="tool-time-calc">
    <!-- 广告位 -->
    <ad-top></ad-top>
    <!-- 简介 -->
    <el-row type="flex" justify="center">
      <el-col :span="20">
        <div class="intro">
          {{ $t('tools.timeClac.introduction') }}
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="9" :offset="2" class="input-form">
        <el-form :model="inputForm" :rules="rules" ref="inputForm" label-width="90px">
          <el-divider content-position="left">{{ $t('tools.timeClac.startZone') }}</el-divider>
          <el-form-item :label="$t('tools.timeClac.startDate')" prop="startDate">
            <el-date-picker v-model="inputForm.startDate" type="date" :placeholder="$t('tools.timeClac.datePlaceholder')"></el-date-picker>
          </el-form-item>
          <el-form-item :label="$t('tools.timeClac.startTime')" prop="startTime">
            <el-time-picker v-model="inputForm.startTime" :placeholder="$t('tools.timeClac.timePlaceholder')"></el-time-picker>
          </el-form-item>
          <el-divider content-position="left">{{ $t('tools.timeClac.finshZone') }}</el-divider>
          <el-form-item :label="$t('tools.timeClac.finshDate')" prop="finshDate">
            <el-date-picker v-model="inputForm.finshDate" type="date" :placeholder="$t('tools.timeClac.datePlaceholder')"></el-date-picker>
          </el-form-item>
          <el-form-item :label="$t('tools.timeClac.finshTime')" prop="finshTime">
            <el-time-picker v-model="inputForm.finshTime" :placeholder="$t('tools.timeClac.timePlaceholder')"></el-time-picker>
          </el-form-item>
          <el-divider content-position="left">{{ $t('tools.timeClac.otpZone') }}</el-divider>
          <el-form-item class="form-item-wrap" :label="$t('tools.timeClac.startZone')">
            <el-input v-model="inputForm.optZone.startZone.day.value" class="opt-zone-input opt-zone-float">
              <el-select v-model="inputForm.optZone.startZone.day.symbol" slot="prepend" class="opt-zone-select">
                <el-option label="+" value="add"></el-option>
                <el-option label="-" value="subtract"></el-option>
              </el-select>
              <template slot="append">{{ $t('tools.timeClac.day') }}</template>
            </el-input>
            <div class="opt-zone-float opt-zone-more">
              <el-button type="text" @click="handleClickMore('startZone')">
                {{ $t('tools.timeClac.more') }}
                <i class="el-icon-arrow-right more-zone-icon"  :class="{ 'more-expanded': inputForm.optZone.startZone.moreExpanded}"></i>
              </el-button>
            </div>
          </el-form-item>
          <!-- 调整-起始-更多 -->
          <el-form-item class="form-item-wrap">
            <collapse>
              <div class="more-zone-wrap" v-show="inputForm.optZone.startZone.moreExpanded">
                <el-input v-model="inputForm.optZone.startZone.year.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.startZone.year.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.year') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.startZone.month.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.startZone.month.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.month') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.startZone.hour.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.startZone.hour.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.hour') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.startZone.minute.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.startZone.minute.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.minute') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.startZone.second.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.startZone.second.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.second') }}</template>
                </el-input>
              </div>
            </collapse>
          </el-form-item>
          <el-form-item class="form-item-wrap last" :label="$t('tools.timeClac.finshZone')">
            <el-input v-model="inputForm.optZone.finshZone.day.value" class="opt-zone-input opt-zone-float">
              <el-select v-model="inputForm.optZone.finshZone.day.symbol" slot="prepend" class="opt-zone-select">
                <el-option label="+" value="add"></el-option>
                <el-option label="-" value="subtract"></el-option>
              </el-select>
              <template slot="append">{{ $t('tools.timeClac.day') }}</template>
            </el-input>
            <div class="opt-zone-float opt-zone-more">
              <el-button type="text" @click="handleClickMore('finshZone')">
                {{ $t('tools.timeClac.more') }}
                <i class="el-icon-arrow-right more-zone-icon" :class="{ 'more-expanded': inputForm.optZone.finshZone.moreExpanded}"></i>
              </el-button>
            </div>
          </el-form-item>
          <!-- 调整-结束-更多 -->
          <el-form-item class="form-item-wrap">
            <collapse>
              <div class="more-zone-wrap" v-show="inputForm.optZone.finshZone.moreExpanded">
                <el-input v-model="inputForm.optZone.finshZone.year.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.finshZone.year.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.year') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.finshZone.month.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.finshZone.month.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.month') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.finshZone.hour.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.finshZone.hour.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.hour') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.finshZone.minute.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.finshZone.minute.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.minute') }}</template>
                </el-input>
                <el-input v-model="inputForm.optZone.finshZone.second.value" class="opt-zone-input more-zone-input">
                  <el-select v-model="inputForm.optZone.finshZone.second.symbol" slot="prepend" class="opt-zone-select">
                    <el-option label="+" value="add"></el-option>
                    <el-option label="-" value="subtract"></el-option>
                  </el-select>
                  <template slot="append">{{ $t('tools.timeClac.second') }}</template>
                </el-input>
              </div>
            </collapse>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="2" class="button-group">
        <p>
          <el-button type="primary" round @click="handleTotal">Go</el-button>
        </p>
      </el-col>
      <el-col :span="9" class="output-form">
        <el-form :model="outputForm" label-width="100px">
          <el-divider content-position="left">{{ $t('tools.timeClac.resultZone') }}</el-divider>
          <el-form-item :label="$t('tools.timeClac.result')">
            <el-input v-model="outputForm.result"></el-input>
          </el-form-item>
          <el-divider content-position="left">{{ $t('tools.timeClac.totalZone') }}</el-divider>
          <el-form-item :label="$t('tools.timeClac.totalDays')">
            <el-input v-model="outputForm.totalDays"></el-input>
          </el-form-item>
          <el-form-item :label="$t('tools.timeClac.totalHours')">
            <el-input v-model="outputForm.totalHours"></el-input>
          </el-form-item>
          <el-form-item :label="$t('tools.timeClac.totalMinutes')">
            <el-input v-model="outputForm.totalMinutes"></el-input>
          </el-form-item>
          <el-form-item :label="$t('tools.timeClac.totalSeconds')">
            <el-input v-model="outputForm.totalSeconds"></el-input>
          </el-form-item>
          <el-divider content-position="left">{{ $t('tools.timeClac.optResultZone') }}</el-divider>
          <el-form-item :label="$t('tools.timeClac.startTime')">
            <el-input v-model="outputForm.resultStartDateTime"></el-input>
          </el-form-item>
          <el-form-item :label="$t('tools.timeClac.finshTime')">
            <el-input v-model="outputForm.resultFinshDateTime"></el-input>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <!-- 广告位 -->
    <ad-bottom></ad-bottom>
  </div>
</template>

<script>
import dayjs from 'dayjs'
import collapse from "../../utils/collapse.js"
import adTop from '../../components/ad/ad-top'
import adBottom from '../../components/ad/ad-bottom'

export default {
  name: "time-calc",
  data() {
    return {
      inputForm: {
        startDate: new Date(),
        finshDate: new Date(),
        startTime: new Date(),
        finshTime: new Date(),
        optZone: {
          startZone: {
            moreExpanded: false,
            year: {
              symbol: "add",
              value: 0
            },
            month: {
              symbol: "add",
              value: 0
            },
            day: {
              symbol: "add",
              value: 0
            },
            hour: {
              symbol: "add",
              value: 0
            },
            minute: {
              symbol: "add",
              value: 0
            },
            second: {
              symbol: "add",
              value: 0
            }
          },
          finshZone: {
            moreExpanded: false,
            year: {
              symbol: "add",
              value: 0
            },
            month: {
              symbol: "add",
              value: 0
            },
            day: {
              symbol: "add",
              value: 0
            },
            hour: {
              symbol: "add",
              value: 0
            },
            minute: {
              symbol: "add",
              value: 0
            },
            second: {
              symbol: "add",
              value: 0
            }
          }
        }
      },
      outputForm: {
        result: '',
        totalDays: 0,
        totalHours: 0,
        totalMinutes: 0,
        totalSeconds: 0,
        resultStartDateTime: '',
        resultFinshDateTime: ''
      },
      rules: {
        startDate: [
          {  type: 'date', required: true, message: this.$t('tools.timeClac.dateRuleRequired'), trigger: 'blur' }
        ],
        finshDate: [
          {  type: 'date', required: true, message: this.$t('tools.timeClac.dateRuleRequired'), trigger: 'blur' }
        ],
        startTime: [
          {  type: 'date', required: true, message: this.$t('tools.timeClac.timeRuleRequired'), trigger: 'blur' }
        ],
        finshTime: [
          {  type: 'date', required: true, message: this.$t('tools.timeClac.timeRuleRequired'), trigger: 'blur' }
        ]
      }
    }
  },
  components: {
    collapse,
    adTop,
    adBottom
  },
  methods: {
    handleTotal() {

      // 校验日期和时间
      this.$refs['inputForm'].validate((valid) => {
          if (!valid) {
            return false;
          }
        });

      // 获取起始日期
      const startDate = this.inputForm.startDate;
      // 获取结束日期
      const finshDate = this.inputForm.finshDate;
      // 获取起始时间
      const startTime = this.inputForm.startTime;
      // 获取结束时间
      const finshTime = this.inputForm.finshTime;

      // 计算完整起始时间，时间戳
      let startTimeFull = startDate.setHours(startTime.getHours(), startTime.getMinutes(), startTime.getSeconds(), 0); 
      // 计算完整结束时间，时间戳
      let finshTimeFull = finshDate.setHours(finshTime.getHours(), finshTime.getMinutes(), finshTime.getSeconds(), 0); 

      // 对调整数据进行处理
      // 起始时间
      const startTimeFullDayJs = this.handleOpt(startTimeFull, this.inputForm.optZone.startZone);
      // 结束时间
      const finshTimeFullDayJs = this.handleOpt(finshTimeFull, this.inputForm.optZone.finshZone);

      startTimeFull = startTimeFullDayJs.valueOf();
      finshTimeFull = finshTimeFullDayJs.valueOf();

      // 计算总秒数
      const totalSeconds = (finshTimeFull - startTimeFull) / 1000;
      // 计算总分钟数
      const totalMinutes = (finshTimeFull - startTimeFull) / 60000;
      // 计算总小时数
      const totalHours = (finshTimeFull - startTimeFull) / 3600000;
      // 计算总天数
      const totalDays = (finshTimeFull - startTimeFull) / 86400000;
      
      // 计算间隔时间
      // 间隔年
      const intervalYear = finshTimeFullDayJs.diff(startTimeFullDayJs, 'year');
      // 间隔月
      const intervalMonth = finshTimeFullDayJs.diff(startTimeFullDayJs, 'month') - intervalYear * 12;
      
      // 间隔日
      // 从开始时间加上间隔月数后的日期
      const startDateAddMonthDayJs = startTimeFullDayJs.add(finshTimeFullDayJs.diff(startTimeFullDayJs, 'month'), 'month');
      const intervalDay = finshTimeFullDayJs.diff(startDateAddMonthDayJs, 'day');
      // 间隔小时
      const intervalHour = Math.floor((finshTimeFull - startTimeFull) % 86400000 / 3600000);
      // 间隔分钟
      const intervalMinute = Math.floor((finshTimeFull - startTimeFull) % 3600000 / 60000);
      // 间隔秒
      const intervalSecond = Math.floor((finshTimeFull - startTimeFull) % 60000 / 1000);

      let result = '';
      if (intervalYear != 0) {
        result += intervalYear + ' ' + this.$t('tools.timeClac.year') + ' ';
      }
      if (intervalMonth != 0) {
        result += intervalMonth + ' ' + this.$t('tools.timeClac.month') + ' ';
      }
      if (intervalDay != 0) {
        result += intervalDay + ' ' + this.$t('tools.timeClac.day') + ' ';
      }
      if (intervalHour != 0) {
        result += intervalHour + ' ' + this.$t('tools.timeClac.hour') + ' ';
      }
      if (intervalMinute != 0) {
        result += intervalMinute + ' ' + this.$t('tools.timeClac.minute') + ' ';
      }
      if (intervalSecond != 0) {
        result += intervalSecond + ' ' + this.$t('tools.timeClac.second');
      }

      // 设置输出显示
      this.outputForm.result = result.trim();
      this.outputForm.totalSeconds = totalSeconds;
      this.outputForm.totalMinutes = totalMinutes;
      this.outputForm.totalHours = totalHours;
      this.outputForm.totalDays = totalDays;
      this.outputForm.resultStartDateTime = startTimeFullDayJs.format('YYYY-MM-DD HH:mm:ss');
      this.outputForm.resultFinshDateTime = finshTimeFullDayJs.format('YYYY-MM-DD HH:mm:ss');
    },
    handleClickMore(zoneId) {
      if ('startZone' === zoneId) {
        const moreExpanded = this.inputForm.optZone.startZone.moreExpanded;
        if (moreExpanded) {
          this.inputForm.optZone.startZone.moreExpanded = false;
        } else {
          this.inputForm.optZone.startZone.moreExpanded = true;
        }
      } else if ('finshZone' === zoneId) {
        const moreExpanded = this.inputForm.optZone.finshZone.moreExpanded;
        if (moreExpanded) {
          this.inputForm.optZone.finshZone.moreExpanded = false;
        } else {
          this.inputForm.optZone.finshZone.moreExpanded = true;
        }
      }
    },
    handleOpt(timeFull, optData) {
      let timeFullDayJs = dayjs(timeFull);
      // 年
      timeFullDayJs = this.handleOptAddOrSubtract(timeFullDayJs, optData.year, 'year');
      // 月
      timeFullDayJs = this.handleOptAddOrSubtract(timeFullDayJs, optData.month, 'month');
      // 日
      timeFullDayJs = this.handleOptAddOrSubtract(timeFullDayJs, optData.day, 'day');
      // 时
      timeFullDayJs = this.handleOptAddOrSubtract(timeFullDayJs, optData.hour, 'hour');
      // 分
      timeFullDayJs = this.handleOptAddOrSubtract(timeFullDayJs, optData.minute, 'minute');
      // 秒
      timeFullDayJs = this.handleOptAddOrSubtract(timeFullDayJs, optData.second, 'second');

      return timeFullDayJs;
    },
    handleOptAddOrSubtract(timeFullDayJs, optData, level) {
      const symbol = optData.symbol;
      const value = optData.value;
      if (value != 0) {
        if ('add' === symbol) {
            timeFullDayJs = timeFullDayJs.add(value, level);
        } else if ('subtract' === symbol) {
            timeFullDayJs = timeFullDayJs.subtract(value, level);
        }
      }
      return timeFullDayJs;
    }
  }
};
</script>

<style scoped>
.input-form, .output-form {
  border-radius: 8px;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.1);
  padding-left: 20px; 
  padding-right: 20px; 
  padding-top: 20px; 
  padding-bottom: 10px; 
}
.input-form {
  padding-bottom: 1.5em;
}
.output-form .el-input {
  width: 300px;
}
.button-group {
  text-align: center;
  padding-top: 14em;
}
.opt-zone-input {
  width: 16em;
}
.opt-zone-select {
  width: 4em;
}
.opt-zone-float {
  float: left;
}
.opt-zone-more {
  padding-left: 1.5em;
}
.more-zone-icon.more-expanded {
    animation-name: more-icon-rotate-expanded;
    animation-duration: 0.3s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
  }
@keyframes more-icon-rotate-expanded {
    0%{ -webkit-transform: rotate(0deg);}
    100%{ -webkit-transform: rotate(90deg);}
}
.more-zone-icon {
    animation-name: more-icon-rotate-collapse;
    animation-duration: 0.3s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
  }
@keyframes more-icon-rotate-collapse {
    0%{ -webkit-transform: rotate(90deg);}
    100%{ -webkit-transform: rotate(0deg);}
}
.more-zone-input {
  padding-top: 1em;
}
.form-item-wrap {
  margin-bottom: 0em;
}
.form-item-wrap.last {
  margin-top: 1.5em;
}
.more-zone-wrap {
  margin-top: 0;
}
.more-zone-active, .more-zone-leave-active {
  transition: all 1s ease;
}
.more-zone-enter, .more-zone-leave-to {
  height: 0;
}
.intro {
  background-color: #F0FFFF;
  padding: 2em;
  border-radius: 8px;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.1);
  color: #00C5CD; 
  margin-bottom: 1em;
  font-size: 0.9em;
}
</style>